import React from 'react';
import { resable } from '@vl/redata/RESABLE';
import RESABLE from '@vl/redata/RESABLE.macro';

import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import { Layout } from 'antd';
import FormProvider from '@uz/unitz-components-web/FormProvider';
import StripeProvider from '@uz/unitz-providers/StripeProvider';
import PaymentPackagesSection from '@uz/unitz-components-web/PaymentPackagesSection';
import PaymentWalletSection from '@uz/unitz-components-web/PaymentWalletSection';
import PaymentMethodsSection from '@uz/unitz-components-web/PaymentMethodsSection';
import PaymentProfileSection from '@uz/unitz-components-web/PaymentProfileSection';
import PaymentBillSection from '@uz/unitz-components-web/PaymentBillSection';
import gstyles from '@vl/gstyles';

const { Sider, Content } = Layout;
const Index = () => {
  // @TODO: validate missing required input
  return (
    <StripeProvider>
      <DIV className="paymentPage">
        <h1 className="text-4xl text-center cursor-pointer text-brandb font-md max-lg:hidden">
          {ctx.apply('i18n.t', 'UserPayment.payment')}
        </h1>
        <FormProvider form={ctx.get('@form')}>
          <Layout className="py-0" style={{ backgroundColor: gstyles.colors.white500 }}>
            <Content className="min-lg:mr-6">
              <div className="min-lg:hidden">
                <PaymentProfileSection />
              </div>
              <PaymentPackagesSection />
              <PaymentWalletSection />
              <RESABLE>
                <DIV>
                  {ctx.debug(() => {
                    const cards = resable.for('cards')(ctx.get('cards'), []) || [];
                    ctx.set('cards', cards);
                  })}
                  <PaymentMethodsSection />
                </DIV>
              </RESABLE>
              <div className="min-lg:hidden">
                <PaymentBillSection />
              </div>
            </Content>
            <Sider className="bg-transparent max-lg:hidden" theme="light" width={360}>
              <PaymentProfileSection />
              <PaymentBillSection />
            </Sider>
          </Layout>
        </FormProvider>
      </DIV>
    </StripeProvider>
  );
};

export default Index;
